// src/functions/session.js
export const createSession = async (advertId) => {
    try {
        const response = await fetch(`https://honeslt.com:8080/sessions/create/${advertId}`, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            }
        });

        if (!response.ok) {
            throw new Error(`: ${response.status}`);
        }

        const data = await response.json();
        return data.sessionId; //  sessionId
    } catch (error) {
        console.error('   :', error);
        throw error; //   
    }
};