// src/functions/api.js
export const fetchAdvertById = async (advertId) => {
    try {
        const response = await fetch(`https://honeslt.com:8080/api/adverts/${advertId}`, {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json'
            }
        });

        if (!response.ok) {
            throw new Error(`: ${response.status}`);
        }

        const data = await response.json();
        return data; //    
    } catch (error) {
        console.error('   :', error);
        throw error; //   
    }
};